.container {
    padding-top: 50px;
    min-height: 100vh;
}

.bank {
    display: flex;
    justify-content: center;
    margin-bottom: 40px;
    font-size: 18px;
}

.box {
    margin-top: 10px;
    width: calc(100% - 20px);
    padding: 15px 0;
    /* background-color: white; */
    text-align: center;
    font-size: 24px;
}



button {
    background-color: var(--tg-theme-button-color);
    border: none;
    color: var(--tg-theme-button-text-color);
    height: 50px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
}




img {
    width: 44px;
}